@import 'libs/html-grid';
@import 'fontawesome-all.min.css';
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900');

// Breakpoints.

@include breakpoints(
	(
		xlarge: (
			1281px,
			1680px,
		),
		large: (
			981px,
			1280px,
		),
		medium: (
			737px,
			980px,
		),
		small: (
			481px,
			736px,
		),
		xsmall: (
			361px,
			480px,
		),
		xxsmall: (
			null,
			360px,
		),
	)
);

// Mixins.

@mixin color($p) {
	@include color-typography($p);
	@include color-box($p);
	@include color-button($p);
	@include color-features($p);
	@include color-form($p);
	@include color-icon($p);
	@include color-list($p);
	@include color-section($p);
	// @include color-table($p);
}

